import React , { Component } from 'react';
import Image from './videos/Enterprise_edge_new.mp4';

class VideoStript extends Component{
    render(){
       let x;
       x= window.innerHeight-100; 
     /*  if(this.props.height <= 200){
           x = 1200;
       }
       else{
           x= 1200;
       }*/
       return (<video loop autoPlay muted style={{
            position:'absolute',
            width:'100%',
            left:'50%',
            top:'55%',
            height:x,
            objectFit:"cover",
            transform:'translate(-50%,-50%)',
            zIndex:'-1',
        }}>
            <source src={Image} type="video/mp4"/>
        </video>
       );
    };
};


export default VideoStript;